import React, { Component } from 'react'
import { connect } from 'react-redux'

import TouchContent from 'Components/touchBox/touchContent'
import LazyImg from 'Extended/LazyImg/LazyImg';

import styles from './SwiperList.scss';

class SwiperList extends Component {
    static defaultProps = {
        cur: 0,
    }
    constructor(props) {
        super(props)
        this.state = {
            show: false,
            cur: props.cur,
        }
    }
    componentWillMount() {
        this.handleReqData(this.props)
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.curcity.get('key') !== this.props.curcity.get('key')) {
            this.props.dispatch('news/save', { swiper: [] })
            this.handleReqData(nextProps)
        }
    }
    async handleReqData(props) {
        let city = props.curcity.get('key')
        let res = await apireq('api/swiper/' + city)
        if (res.success) {
            this.setState({
                cur: 0
            })
            this.props.dispatch('news/save', { swiper: res.data })
        }
    }
    handleTouchEnd = (cur) => {
        this.setState({
            cur
        })
    }
    render() {
        let { swiper } = this.props
        return (
            swiper && swiper.size > 0 ?
                <div className={styles.swiper}>
                    <TouchContent style={{ height: '150px' }} onTouchEnd={this.handleTouchEnd} cur={this.state.cur} >
                        {swiper.map((item, index) => {
                            let rest = null
                            switch (item.get('type')) {
                                case 'video':
                                    rest = <video key={index} className={styles.slide} controls preload="auto"
                                        x-webkit-airplay='true' x5-video-player-type='h5' x5-video-player-fullscreen='true'
                                        webkit-playsinline='true' playsInline='true' src={item.get('url')} />
                                    break;
                                default:
                                    rest = <div className={styles.slide} key={index}>
                                        <LazyImg src={item.get('image')} show={this.state.cur == index} />
                                        <div className={styles.p}>{item.get('title')}</div>
                                    </div>
                                    break;
                            }
                            return rest
                        })}
                    </TouchContent>
                    <div className={styles.curtab}>{(this.state.cur + 1) + '/' + swiper.size}</div>
                </div> :
                <div className={styles.swiper}>
                    <div className={styles.loading}>loading</div>
                </div>
        )
    }
}

export default connect((state) => ({
    curcity: state.getIn(['init', 'curcity']),
    swiper: state.getIn(['news', 'swiper']),
}))(SwiperList)


