import styles from './touchContent.scss';
import React from 'react';

class TouchContent extends React.Component {
    static defaultProps = {
        length: 3,
        cur: 0,
    }
    constructor(props) {
        super(props);
        this.moveXY = 0
        this._moveX = 0
        this.state = {
            cur: props.cur,
        }
        this._onTouchStart = this._onTouchStart.bind(this)
        this._onTouchMove = this._onTouchMove.bind(this)
        this._onTouchEnd = this._onTouchEnd.bind(this)
    }
    componentDidMount() {
        this._warper = this.refs.warper
        this._width = this._warper.getBoundingClientRect().width;
    }
    _translate(dist, speed) {
        this._warper.style.webkitTransitionDuration = this._warper.style.MozTransitionDuration =
            this._warper.style.msTransitionDuration = this._warper.style.OTransitionDuration =
            this._warper.style.transitionDuration = speed + 'ms';
        this._warper.style.transform = 'translateX(' + dist + 'px)';
    }
    _onTouchStart(e) {
        let point = e.touches[0] || e;
        this._startX = point.pageX;
        this._startY = point.pageY;
    }
    _onTouchMove(e) {
        let point = e.touches[0] || e;
        this._moveX = point.pageX - this._startX;
        let moveY = point.pageY - this._startY;
        if (this.moveXY == 0) {
            if (Math.abs(this._moveX) > Math.abs(moveY)) {
                this.moveXY = 1
            } else {
                this.moveXY = 2
            }
        }
        if (this.moveXY !== 1) {
            return
        }
        let { cur } = this.props
        let dist = 0.4 * this._moveX - cur * this._width
        this._translate(dist, 0)
        this.arrow = this._moveX > 0 ? 1 : -1
        this.props.touchX && this.props.touchX(this._moveX)
    }
    _onTouchEnd(e) {
        let _moveX = Math.abs(this._moveX)
        this.moveXY = 0
        this._moveX = 0
        let length = this.props.children.length || this.props.children.size || this.props.length
        let { cur } = this.props
        if (!((cur == 0 && this.arrow == 1) || (cur == length - 1 && this.arrow == -1) || _moveX < 100)) {
            cur = cur - this.arrow
        }
        let dist = - cur * this._width
        this._translate(dist, 200)
        if (_moveX < 100) {
            return
        }
        this.props.onTouchEnd && this.props.onTouchEnd(cur)
    }
    render() {
        let { children, cur, style } = this.props
        let warperstyle = {
            transform: 'translateX(-' + cur * 100 + '%)'
        }
        return (
            <div className={styles.warp} style={style} >
                <div className={styles.warper}
                    style={warperstyle}
                    ref='warper'
                    onTouchStart={this._onTouchStart}
                    onTouchMove={this._onTouchMove}
                    onTouchEnd={this._onTouchEnd}>
                    {this.props.children.map((child, i) => {
                        let translateX = i * 100 + '%'
                        return (
                            <div className={styles.children} key={i} style={{ transform: ' translateX(' + translateX + ')' }}>
                                {child}
                            </div>
                        )
                    })}
                </div>
            </div>
        )
    }
}

export default TouchContent


